<template>
  <div class="weui_msg">
    <div class="weui_icon_area">
      <i class="weui_icon_info weui_icon_msg"></i>
    </div>
    <div class="weui_text_area">
      <h4 class="weui_msg_title">请在微信客户端打开链接</h4>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  async mounted() {
    this.hiddebeginloading();
  },
  methods: {
    //隐藏页面首次加载的loading画面
    hiddebeginloading() {
      document.getElementById("beginloading").style.display = "none";
    }
  }
};
</script>


<style>
@font-face {
  font-weight: normal;
  font-style: normal;
  font-family: "weui";
  src: url("data:application/octet-stream;base64,")
    format("truetype");
}
[class^="weui_icon_"]:before,
[class*=" weui_icon_"]:before {
  font-family: "weui";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  vertical-align: middle;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  /* opacity: .8; */
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: 0.2em;
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}
.weui_icon_info:before {
  content: "\EA03";
}
.weui_icon_info_circle:before {
  content: "\EA0C";
}
.weui_icon_info_circle:before {
  font-size: 23px;
  color: #09bb07;
}
.weui_icon_info:before {
  font-size: 23px;
  color: #10aeff;
}
.weui_msg {
  padding-top: 36px;
  text-align: center;
}
.weui_msg .weui_icon_area {
  margin-bottom: 30px;
}
.weui_msg .weui_text_area {
  margin-bottom: 25px;
  padding: 0 20px;
}
.weui_msg .weui_msg_title {
  margin-bottom: 5px;
  font-weight: 400;
  font-size: 20px;
}
.weui_msg .weui_msg_desc {
  font-size: 14px;
  color: #888;
}
.weui_msg .weui_opr_area {
  margin-bottom: 25px;
}
.weui_msg .weui_extra_area {
  margin-bottom: 15px;
  font-size: 14px;
  color: #888;
}
.weui_msg .weui_extra_area a {
  color: #61749b;
}

.weui_icon_msg:before {
  font-size: 104px;
}
.weui_icon_warn.weui_icon_msg:before {
  color: #f76260;
}
</style>




