import sitecfg from '@/config.js'
import index from '@/components/index.vue'
import error from '@/components/error.vue'
import fun from "@/components/test.vue"
import axios from 'axios'

export default [
    // {
    //     path:'/funselect/',
    //     component:fun
    // },
    {
        path: '/binder/',
        component: index
    },
    {
        path: '/error/',
        component: error,
    },
    {
        path: '/',
        redirect: async function (route, resolve, reject) {
            //加密解密配置
            // let xx=crypto.AES.encrypt(JSON.stringify(sitecfg),'simple')
            // let debyte=crypto.AES.decrypt(xx,'simple')

            // console.log(xx+"==="+JSON.parse(debyte.toString(crypto.enc.Utf8)))
            // alert(sitecfg.serverURL)
            
            // return

            // var ua = navigator.userAgent.toLowerCase();
            // var isWeixin = ua.indexOf('micromessenger') != -1;
            // var isAndroid = ua.indexOf('android') != -1;
            // var isIos = (ua.indexOf('iphone') != -1) || (ua.indexOf('ipad') != -1);
            // if (!isWeixin) {
            //     resolve('/error/')
            //     return
            // }
            
            if (route.query.code) {
                //alert(sitecfg.serverURL + '/' + sitecfg.valiwechatcodePATH + '?code=' + route.query.code)
                window.location = sitecfg.serverURL + sitecfg.valiwechatcodePATH + '?code=' + route.query.code;
                return;
            } else if (route.query.token) {
                try {
                    let res = await axios.post(
                        sitecfg.serverURL + sitecfg.valitoken,
                        null,
                        {
                            headers: {
                                Authorization: route.query.token
                            }
                        }
                    );
                    if (res.data.vali) {
                        sessionStorage.setItem("token", route.query.token)
                        if (res.data.isbinder)
                            window.location=sitecfg.clientURL+sitecfg.binderokPATH
                            //resolve('/funselect/')
                        else
                            resolve('/binder/')
                    } else
                        resolve("/error/")
                } catch (err) {
                    alert(err)
                }
                return
            } else {
                resolve("/error/")
                return
            }
        }
    }
]


